.Wrapper {
  width: 80%;
  height: 60%;
  margin: 0 auto;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Message {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  color: black;
  margin-bottom: 30px;
}

.GetDataButton {
  width: 150px;
  height: 50px;

  border: 1px solid rgba(0, 128, 0, 0.3);
  border-radius: 4px;

  background-color: rgba(0, 128, 0, 0.3);

  cursor: pointer;
}
