.Paginator {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.Paginator button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 0, 255, 0.3);
  border-radius: 4px;
  cursor: pointer;
}

.Paginator button.ActivePage {
  background-color: rgba(0, 0, 255, 0.3);
  color: white;
}
